*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    background-color: aqua;
}

:root {
    --primary-color: #e60023;
    --primary-background: rgba(0, 0, 0, 0.15);
    --primary-transition-time: 150ms;
}

#root {
    width: 90%;
    height: calc(100vh - 20px);
    margin: 10px auto;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0px 5px 5px 3px rgba(0, 0, 0, 0.6);
    overflow: hidden;
}

/* Header */
.heading {
    background-color: var(--primary-color);
    color: white;
    padding: 1rem 2rem;
    font-size: 2.2rem;
    height: 10%;
}

/* Main */
.main {
    height: 90%;
    display: grid;
    grid-template-columns: max-content 1fr;
}

/* Sidebar */
.sidebar {
    height: 100%;
    padding: 2rem 1rem;
    border-right: 1px solid var(--primary-color);
    position: relative;
    overflow-y: auto;
}

.sidebar ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.sidebar a {
    display: block;
    text-decoration: none;
    font-size: 1.5rem;
    color: #000;
    padding-block: 0.7rem;
    padding-inline: 1rem 6rem;
    border-radius: 4px;
    transition: background-color ease-in-out var(--primary-transition-time);
    position: relative;
}

.container-project {
    margin-left: 1rem;
}

.sidebar a.active,
.sidebar a:hover,
.sidebar a:focus {
    background-color: var(--primary-background);
}

/* Container */
.container {
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 2rem;
    overflow: auto;
}

/* Todo Item */
.item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1.2rem;
    background-color: var(--primary-background);
    font-size: 1.15rem;
    padding: 0.8rem 1rem;
    transition: transform ease-in-out var(--primary-transition-time), box-shadow ease-in-out var(--primary-transition-time);
    border-radius: 4px;
}


.item:hover,
.item:focus {
    transform: scale(1.003);
    box-shadow: 6px 6px 4px 0px rgba(0, 0, 0, 0.3);
}

.info-container {
    flex: 1;
}

.info-container,
.actions-container {
    display: flex;
    align-items: center;
    gap: 1rem
}

input[type="checkbox"] {
    appearance: none;
    width: 1.4rem;
    height: 1.4rem;
    border: 1px solid #000;
    border-radius: 2px;
    cursor: pointer;
    position: relative;
}

input[type="checkbox"]:checked {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}), var(--primary-color);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.item:has(#completed:checked) .title {
    text-decoration: line-through;
}

.item:has(#completed:checked) .date-created,
.item:has(#completed:checked) .edit,
.item:has(#completed:checked) .delete,
.item:has(#completed:checked) .detail {
    filter: opacity(0.4);
}

.item .title {
    font-size: 1.2rem;
}

.item .date-created {
    margin-left: auto;
    text-decoration: underline;
    font-style: italic;
    font-size: 1rem;
}

.edit,
.delete {
    font-size: 1.5rem;
    cursor: pointer;
    transition: color ease-in-out var(--primary-transition-time);
}

.edit:hover,
.edit:focus,
.delete:hover,
.delete:focus {
    color: var(--primary-color);
}

/* Projects */
.projects {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.project {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.project:has(button.delete-project:hover)>.project-title {
    text-decoration: line-through;
}

.project-title {
    font-size: 1.5rem;
}

.project>.item {
    margin-left: 1rem;
}

/* Popup */
.popup {
    position: absolute;
    inset: 0;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(4px);
}


/* Detail , Editor, Create */
.create,
.detail,
.editor {
    width: max-content;
    height: auto;
    background-color: white;
    padding: 3rem 4rem;
    border-radius: 4px;
    font-size: 1.8rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    position: relative;
    overflow: hidden;
}

.detail>p,
.editor>label {
    display: grid;
    grid-template-columns: 200px 1fr;
    align-content: center;
    align-items: center;
    gap: 1rem;
}

.detail>p>span,
.editor>label>span {
    font-weight: bolder;
}

.title {
    font-size: 2.4rem;
}

.create>.btn-close,
.detail>.btn-close,
.editor>.btn-close {
    position: absolute;
    top: 0.4rem;
    right: 0.8rem;
    cursor: pointer;
}

.editor input:not([type="checkbox"]) {
    width: 400px;
    font-size: 1rem;
    padding: 0.8rem 1rem;
    outline: none;
    border: 2px solid var(--primary-color);
    border-radius: 4px;
}

.editor input:not([type="checkbox"]):focus {
    outline: 1px solid var(--primary-color);
}

.editor .btn-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 2rem;
}


/* Create */
.create {
    padding: 0;
    display: grid;
    grid-template-columns: auto 1fr;
    gap: 0;
}

.create .heading {
    grid-column: 1/-1;
    height: max-content;
}

.create > .sidebar {
    padding: 1rem;
}

.create > .sidebar a {
    padding-inline: 2rem 4rem;
}

.create .btn-close {
    color: white;
}

.create > .editor {
    min-height: 550px;
    position: relative;
}

.create > .editor select {
    width: 400px;
    font-size: 1rem;
    padding: 0.8rem 1rem;
    outline: none;
    border: 2px solid var(--primary-color);
    border-radius: 4px;
}

.create > .editor > .btn {
    position: absolute;
    bottom: 20px;
}

/* notification */
.notification {
    position: absolute;
    top: 50%;
    right: 0.4rem;
    transform: translateY(-50%);
    background-color: var(--primary-color);
    color: white;
    font-size: 1rem;
    padding: 0.5rem;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Utils */
.btn {
    border: none;
    outline: none;
    background-color: transparent;
    color: white;
    font-size: 1rem;
    border-radius: 0.5em;
    padding: 0.4em 1em;
    cursor: pointer;
    border: 2px solid var(--primary-color);
    text-transform: uppercase;
    color: var(--primary-color);
    transition: background-color ease-in-out var(--primary-transition-time), color ease-in-out var(--primary-transition-time);
}

.btn:hover,
.btn:focus {
    background-color: var(--primary-color);
    color: white;
}

.btn-add {
    padding: 0;
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
    font-size: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translateX(-50%);
}

.border-left-1 {
    border-left: 6px solid hsl(120, 100%, 50%);
}

.border-left-2 {
    border-left: 6px solid hsl(60, 95%, 50%);
}

.border-left-3 {
    border-left: 6px solid hsl(0, 100%, 60%);
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;;;IAGI,sBAAsB;IACtB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,qGAAqG;IACrG,sBAAsB;AAC1B;;AAEA;IACI,wBAAwB;IACxB,yCAAyC;IACzC,gCAAgC;AACpC;;AAEA;IACI,UAAU;IACV,0BAA0B;IAC1B,iBAAiB;IACjB,uBAAuB;IACvB,kBAAkB;IAClB,8CAA8C;IAC9C,gBAAgB;AACpB;;AAEA,WAAW;AACX;IACI,sCAAsC;IACtC,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;IACjB,WAAW;AACf;;AAEA,SAAS;AACT;IACI,WAAW;IACX,aAAa;IACb,sCAAsC;AAC1C;;AAEA,YAAY;AACZ;IACI,YAAY;IACZ,kBAAkB;IAClB,4CAA4C;IAC5C,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,cAAc;IACd,qBAAqB;IACrB,iBAAiB;IACjB,WAAW;IACX,qBAAqB;IACrB,yBAAyB;IACzB,kBAAkB;IAClB,uEAAuE;IACvE,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;;;IAGI,2CAA2C;AAC/C;;AAEA,cAAc;AACd;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,aAAa;IACb,cAAc;AAClB;;AAEA,cAAc;AACd;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,WAAW;IACX,2CAA2C;IAC3C,kBAAkB;IAClB,oBAAoB;IACpB,uHAAuH;IACvH,kBAAkB;AACtB;;;AAGA;;IAEI,uBAAuB;IACvB,8CAA8C;AAClD;;AAEA;IACI,OAAO;AACX;;AAEA;;IAEI,aAAa;IACb,mBAAmB;IACnB;AACJ;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,cAAc;IACd,sBAAsB;IACtB,kBAAkB;IAClB,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,yEAAmE;IACnE,2BAA2B;IAC3B,4BAA4B;IAC5B,sBAAsB;AAC1B;;AAEA;IACI,6BAA6B;AACjC;;AAEA;;;;IAII,oBAAoB;AACxB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;IACjB,0BAA0B;IAC1B,kBAAkB;IAClB,eAAe;AACnB;;AAEA;;IAEI,iBAAiB;IACjB,eAAe;IACf,4DAA4D;AAChE;;AAEA;;;;IAII,2BAA2B;AAC/B;;AAEA,aAAa;AACb;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;AACrB;;AAEA,UAAU;AACV;IACI,kBAAkB;IAClB,QAAQ;IACR,YAAY;IACZ,oCAAoC;IACpC,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,0BAA0B;AAC9B;;;AAGA,4BAA4B;AAC5B;;;IAGI,kBAAkB;IAClB,YAAY;IACZ,uBAAuB;IACvB,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;;IAEI,aAAa;IACb,gCAAgC;IAChC,qBAAqB;IACrB,mBAAmB;IACnB,SAAS;AACb;;AAEA;;IAEI,mBAAmB;AACvB;;AAEA;